import React from "react";
import HomeIcon from '@mui/icons-material/Home';
import CurrencyExchangeIcon from '@mui/icons-material/CurrencyExchange';
import AgricultureIcon from '@mui/icons-material/Agriculture';
import AutoGraphIcon from '@mui/icons-material/AutoGraph';
import LocalDrinkIcon from '@mui/icons-material/LocalDrink';
import MergeIcon from '@mui/icons-material/Merge';
import OpacityIcon from '@mui/icons-material/Opacity';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import AddReactionIcon from '@mui/icons-material/AddReaction';

import { Link } from 'react-router-dom';

// import react pro sidebar components
import {
  ProSidebar,
  Menu,
  SubMenu,
  MenuItem,
  SidebarHeader,
  SidebarFooter,
  SidebarContent,
} from "react-pro-sidebar";

// import sidebar css from react-pro-sidebar module and our custom css 
import "react-pro-sidebar/dist/css/styles.css";
import "./sidebar.css";

export interface SideBarProps {
  menuCollapse: boolean,
  setMenuCollapse: (value: boolean) => void;
}


const SideBar = ({menuCollapse, setMenuCollapse}:SideBarProps) => {
  
    // create a custom function that will change menucollapse state from false to true and true to false
  const menuIconClick = () => {
    // condition checking to change state from true to false and vice versa
    // eslint-disable-next-line no-unused-expressions
    menuCollapse ? setMenuCollapse(false) : setMenuCollapse(true);
  };

  return (
    <>
      <div id="header" className="side-bar">
        {/* collapsed props to change menu size using menucollapse state */}
        <ProSidebar collapsed={menuCollapse}>
          
          <SidebarContent>
            <Menu iconShape="square">
              <MenuItem active icon={<HomeIcon/>}>
                Home
                <Link to="/crowd-loan-app" />
              </MenuItem>
              <MenuItem active icon={<CurrencyExchangeIcon />}>
                Swap
                <Link to="/crowd-swap" />
              </MenuItem>
              <MenuItem active icon={<LocalDrinkIcon />}>
                Pool
              </MenuItem>
              <MenuItem active icon={<AgricultureIcon />}>
                Farm
              </MenuItem>
              <MenuItem active icon={<OpacityIcon />}>
                Stake
              </MenuItem>
              <MenuItem active icon={<MergeIcon />}>
                Bridge
              </MenuItem>
              <MenuItem active icon={<AutoGraphIcon />}>
                Analytics
              </MenuItem>
              <MenuItem active icon={<ThumbUpIcon />}>
                Crowdloan
              </MenuItem>
              <MenuItem active icon={<AddReactionIcon />}>
                Mint LUSD
              </MenuItem>
              {/* <SubMenu title="Request" icon={<img alt="request" src="/image/sidebar/item-request.svg" />}>
                <MenuItem icon={<img alt="invoice" src="/image/sidebar/item-invoice.svg" />}>
                  Invoice Approval
                </MenuItem>
                <MenuItem icon={<img alt="move" src="/image/sidebar/item-move.svg" />}>Enrollment Request</MenuItem>
                <MenuItem icon={<img alt="payment" src="/image/sidebar/item-payment.svg" />}>Payment Request</MenuItem>
              </SubMenu>   */}


            </Menu>
          </SidebarContent>
          <SidebarFooter>
            <Menu iconShape="square">
              <MenuItem icon={<img alt="logout" src="/image/sidebar/out.svg" />}>Logout</MenuItem>
            </Menu>
          </SidebarFooter>
        </ProSidebar>
      </div>
    </>
  );
};

export default SideBar;