import { TextField, Slider, Chip, Button } from "@mui/material";
import {SwapInput} from './SwapInput';

import styles from './Swap.module.scss';
import { useState } from "react";
export const Swap = () => {

  const [from_value, setFromValue] = useState(0.0);
  const [to_value, setToValue] = useState(0.0);

  const [from_type, setFromType] = useState("LEOS");
  const [to_type, setToType] = useState("BNB");

  const [is_wallet_connected, setWalletConnected] = useState(0);

  const swapInputPos = () => {
    let tmp_value = from_value;
    setFromValue(to_value);
    setToValue(tmp_value);

    let tmp_type = from_type;
    setFromType(to_type);
    setToType(tmp_type);
  }

  return (
    <div className={styles.main}>
      <h1 className={styles.title}>Swap</h1>
      <div className={styles.form}>
        <div className={styles.form_header}>
          <div className={styles.subtitle}>
            <h2 className={styles.subtitle_name}>Exchange</h2>
            <p className={styles.subtitle_desc}>Trade tokens in an instant</p>
          </div>
          <div className={styles.form_btns}>
            <button>
            <svg viewBox="0 0 24 24" color="primary" width="24px" xmlns="http://www.w3.org/2000/svg" className="sc-bdnxRM dFflPr"><path d="M0 0h24v24H0z" fill="none"></path><path d="M3 17v2h6v-2H3zM3 5v2h10V5H3zm10 16v-2h8v-2h-8v-2h-2v6h2zM7 9v2H3v2h4v2h2V9H7zm14 4v-2H11v2h10zm-6-4h2V7h4V5h-4V3h-2v6z"></path></svg>
            </button>
            <button>
            <svg viewBox="0 0 24 24" color="primary" width="24px" xmlns="http://www.w3.org/2000/svg" className="sc-bdnxRM dFflPr"><path d="M13 3C8.03 3 4 7.03 4 12H2.20711C1.76165 12 1.53857 12.5386 1.85355 12.8536L4.54604 15.546C4.73751 15.7375 5.04662 15.7418 5.24329 15.5556L8.08805 12.8631C8.4164 12.5524 8.19646 12 7.74435 12H6C6 8.13 9.13 5 13 5C16.87 5 20 8.13 20 12C20 15.87 16.87 19 13 19C11.4314 19 9.98175 18.4782 8.81739 17.601C8.37411 17.267 7.74104 17.259 7.3486 17.6514C6.95725 18.0428 6.95413 18.6823 7.38598 19.0284C8.92448 20.2615 10.8708 21 13 21C17.97 21 22 16.97 22 12C22 7.03 17.97 3 13 3ZM12 8V13L16.28 15.54L17 14.33L13.5 12.25V8H12Z"></path></svg>
            </button>
          </div>
        </div>
        <div className={styles.form_body}>
          <SwapInput 
            label={"From"} 
            value = {from_value} 
            onChangeValue = {setFromValue} 
            type = {from_type} 
            onChangeType = {setFromType}
          />
          <div className={styles.trans_btn}>
            <button onClick={swapInputPos}>
              <svg viewBox="0 0 24 25" color="primary" width="30px" xmlns="http://www.w3.org/2000/svg" className="sc-bdfBwQ hwDTYX"><path d="M11 5V16.17L6.11997 11.29C5.72997 10.9 5.08997 10.9 4.69997 11.29C4.30997 11.68 4.30997 12.31 4.69997 12.7L11.29 19.29C11.68 19.68 12.31 19.68 12.7 19.29L19.29 12.7C19.68 12.31 19.68 11.68 19.29 11.29C18.9 10.9 18.27 10.9 17.88 11.29L13 16.17V5C13 4.45 12.55 4 12 4C11.45 4 11 4.45 11 5Z"></path></svg>
            </button>
          </div>
          <SwapInput 
            label={"To"} 
            value = {to_value} 
            onChangeValue = {setToValue} 
            type = {to_type} 
            onChangeType = {setToType}/>
        </div>
        <div className={styles.form_footer}>
          <button>{is_wallet_connected?"Confirm":"Connect Wallet"}</button>
        </div>
       </div>
    </div>
  );
};
