import React, { useState } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { Home } from "../pages/Home";
import { CrowdloanHome } from "../pages/CrowdloanHome";
import { Registration } from "../pages/Registration";
import { StatusTable } from "../pages/StatusTable";
import { Swap } from "../pages/Swap";
import { Stake } from "../pages/Stake"
import Sidebar from "../components/Sidebar";
import ButtonAppBar from "../components/Header";
import { isMobile } from 'react-device-detect';

export const App = () => {

  const [menuCollapse, setMenuCollapse] = useState(false);

  return (
    <>
    <BrowserRouter>
      <ButtonAppBar menuCollapse = {menuCollapse} setMenuCollapse = {setMenuCollapse}/>
      <Sidebar menuCollapse = {menuCollapse} setMenuCollapse = {setMenuCollapse}/>
      <div style={{paddingLeft:((menuCollapse || isMobile) ? "80px" : "240px"), transition : "0.3s"}}>
        
          <Routes>
            <Route path="/crowd-loan-app" element={<Home />} />
            <Route path="/crowd-loan-info" element={<CrowdloanHome />} />
            <Route path="/registration" element={<Registration />} />
            <Route path="/crowd-status" element={<StatusTable />} />
            <Route path="/crowd-swap" element={<Swap />} />
            <Route path="/crowd-stake" element={<Stake />} />
            {/* <Route path="*" element={<NotFound/>}/> */}
          </Routes>
        
      </div>
      </BrowserRouter>
    </>
  );
};
