
import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import MenuOpenIcon from '@mui/icons-material/MenuOpen';
import './navbar.css';


export interface SideBarProps {
  menuCollapse: boolean,
  setMenuCollapse: (value: boolean) => void;
}


export default function ButtonAppBar({menuCollapse, setMenuCollapse}:SideBarProps) {

  const menuIconClick = () => {
    // condition checking to change state from true to false and vice versa
    // eslint-disable-next-line no-unused-expressions
    menuCollapse ? setMenuCollapse(false) : setMenuCollapse(true);
  };

  return (
    <Box sx={{ flexGrow: 1 }} style={{position:"sticky", top:0, zIndex:"100"}}>
      <AppBar position="static">
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
          >
            {menuCollapse ? <MenuIcon onClick = {menuIconClick}/> : <MenuOpenIcon onClick = {menuIconClick}/>}
            
          </IconButton>
          <Typography className = "logo" variant="h6" component="div" sx={{ flexGrow: 1 }}>
            CROWD
          </Typography>
          <Button color="inherit" className = "connect_button">Connect</Button>
        </Toolbar>
      </AppBar>
    </Box>
  );
}
