import  styles from './SwapInput.module.scss';

export interface TokenProps {
  label: string
}

export const TokenItem = ({label}:TokenProps) => {

  return (
    <div className={styles.token_item}>
        <div className={styles.token_img}>
            <img alt="token icon" src="/image/eos-coin.svg"/>
        </div>
        <div>
            <span>{label}</span>
        </div>
    </div>
  );
};
