import { Button, Modal, Box, Typography } from "@mui/material";
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import React, { useState } from "react";

import { TokenItem } from "./TokenItem";

import  styles from './SwapInput.module.scss';

export interface SwapProps {
  label: string,
  value: number,
  onChangeValue: (value: number) => void;
  type: string,
  onChangeType: (value: string) => void;
}

const style = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,

};

const rx_live = /^[+-]?\d*(?:[.,]\d*)?$/;

export const SwapInput = ({label, value, onChangeValue, type, onChangeType}:SwapProps) => {
const [open, setOpen] = useState(false);

const handleClose = () => {
  setOpen(false);
}
const handleOpen = () => {
  setOpen(true);
}

const onChangeTagInput = (e:any) => {
  if (rx_live.test(e.target.value)) {
    onChangeValue(e.target.value);
  }
}

  return (

    <>
      <div>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
          className = {styles.modal_box}
        >
          <Box sx={style} className={styles.token_modal}>
            <div className = {styles.modal_header}>
              <Typography id="modal-modal-title" variant="h6" component="h2">
                Select a Token
              </Typography>
              <IconButton
                aria-label="close"
                onClick={handleClose}
                sx={{
                  position: 'absolute',
                  right: 8,
                  top: 8,
                  color: (theme) => theme.palette.grey[500],
                }}
              >
                <CloseIcon/>
              </IconButton>
            </div>
            <div className = {styles.modal_body}>
              <input placeholder = {"Search name or paste address"}/>
              <div className={styles.token_view} id="modal-modal-description">
                <div>
                  <TokenItem label={"BNB"}/>
                  <TokenItem label={"ADA"}/>
                  <TokenItem label={"ANKR"}/>
                  <TokenItem label={"USDT"}/>
                  <TokenItem label={"BUSD"}/>
                  <TokenItem label={"CAKE"}/>
                  <TokenItem label={"BNB"}/>
                  <TokenItem label={"ADA"}/>
                  <TokenItem label={"ANKR"}/>
                  <TokenItem label={"USDT"}/>
                  <TokenItem label={"BUSD"}/>
                </div>
              </div>
            </div>
          </Box>
        </Modal>
      </div>
      
      <div className = {styles.swap_currency_input}>
        <div className={styles.title}>{label}</div>
        <div className={styles.main}>
          <input value = {value} pattern="[+-]?\d+(?:[.,]\d+)?" onChange={onChangeTagInput} maxLength={30}/>
          <button onClick={handleOpen}>
            <div className={styles.token_btns}>
              <img className="sc-ehALMs iCnozE" width={"24px"} alt="RUBY logo" src="https://raw.githubusercontent.com/top1st/token-assets/main/png/ruby.png"/>
              <div>
                {type}
              </div>
              <svg viewBox="0 0 24 24" color="text" width="20px" xmlns="http://www.w3.org/2000/svg" className="sc-bdnxRM cXNIfS"><path d="M8.11997 9.29006L12 13.1701L15.88 9.29006C16.27 8.90006 16.9 8.90006 17.29 9.29006C17.68 9.68006 17.68 10.3101 17.29 10.7001L12.7 15.2901C12.31 15.6801 11.68 15.6801 11.29 15.2901L6.69997 10.7001C6.30997 10.3101 6.30997 9.68006 6.69997 9.29006C7.08997 8.91006 7.72997 8.90006 8.11997 9.29006Z"></path></svg>
            </div>
          </button>
        </div>
      </div>
    </>
  );
};
